import React from 'react';

const ProjectInformationForm = ({ projectInfo, setProjectInfo }) => {
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setProjectInfo({
      ...projectInfo,
      [name]: value,
    });
  };

  return (
    <div className="project-info-form">
      <div className="input-row">
        <label>Project Title:</label>
        <input
          type="text"
          name="title"
          value={projectInfo.title}
          onChange={handleInputChange}
        />
      </div>
      <div className="input-row">
        <label>Designer's Name:</label>
        <input
          type="text"
          name="designer"
          value={projectInfo.designer}
          onChange={handleInputChange}
        />
      </div>
      <div className="input-row">
        <label>Department:</label>
        <input
          type="text"
          name="department"
          value={projectInfo.department}
          onChange={handleInputChange}
        />
      </div>
      <div className="input-row">
        <label>Date:</label>
        <input
          type="text"
          name="date"
          value={projectInfo.date}
          readOnly
        />
      </div>
    </div>
  );
};

export default ProjectInformationForm;
